* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  box-shadow: 0 0 12px 0 #018475;
  background-color: #018475;
}
.header__content {
  display: flex;
  justify-content: space-around;
}
.header__logo_bar a {
  text-decoration: none;
  color: white;
  animation: fooocus 2s ease-in 1s infinite;
}
@keyframes fooocus {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.header__list {
  display: flex;
}
.header__list li {
  margin-left: 15px;
  list-style-type: none;
}
.header__list li a {
  text-decoration: none;
  color: white;
  padding: 6px 10px;
  transition: 0.2s ease-in;
}
.header__list li a:hover {
  color: #018475;
  border-radius: 6px;
  background-color: white;
}

@media screen and (max-width: 991.99px) {
  .header__list {
    display: none;
  }
}
footer {
  background-color: #018475;
  padding: 10px;
}

.footer__logo img {
  width: 50px;
  object-fit: cover;
  border-radius: 6px;
}
.footer__logo span {
  color: white;
  font-size: 15px;
}
.footer__nav li {
  list-style-type: none;
  margin-top: 5px;
}
.footer__nav a {
  text-decoration: none;
  padding: 6px;
  color: white;
  transition: 0.1s ease-in;
}
.footer__nav a:hover {
  background-color: white;
  border-radius: 6px;
  color: #018475;
}
.footer__social li {
  list-style-type: none;
}
.footer__social li a {
  text-decoration: none;
  color: white;
  padding: 5px;
  margin-left: 5px;
}
.footer__social li a:hover {
  background-color: white;
  border-radius: 6px;
  color: #018475;
}
.footer__social li i {
  color: white;
  font-size: 18px;
}

@media screen and (max-width: 768.99px) {
  .footer__logo span {
    font-size: 13px;
  }
  .footer__nav ul {
    padding-left: 0;
  }
}
.services__head h3 {
  font-family: serif;
}
.services__head h3 span {
  color: #018475;
  font-size: 18px;
  font-family: "Courier New", Courier, monospace;
}
.services__box .carts {
  background-color: #018475;
  padding: 10px;
  color: white;
  border-radius: 6px;
}
.services__box .carts__heading {
  text-align: center;
}
.services__box .carts__heading i {
  font-size: 30px;
}
.services__box .carts__heading h6 {
  font-family: cursive;
}
.services__box .carts__content img {
  width: 100%;
  opacity: 0.7;
  height: 180px;
  border-radius: 20px;
  object-fit: cover;
  padding: 10px 0px 10px 0px;
}

.about h3 {
  font-family: serif;
}
.about h3 span {
  color: #018475;
  font-size: 18px;
  font-family: "Courier New", Courier, monospace;
}

.nihad {
  text-align: center;
}
.nihad hr {
  color: #018475;
}
.nihad__photos img {
  border-radius: 200px;
  width: 250px;
}
.nihad__buttons a {
  background-color: #018475;
  color: white;
  transition: 0.2s ease-in-out;
  border-radius: 4px;
  padding: 10px;
  text-decoration: none;
}
.nihad__buttons a:hover {
  color: #018475;
  background-color: white;
  border: 2px solid #018475;
}

.huseyn {
  text-align: center;
}
.huseyn__photos img {
  border-radius: 200px;
  width: 236.5px;
}
.huseyn__buttons a {
  background-color: #018475;
  color: white;
  transition: 0.2s ease-in-out;
  border-radius: 4px;
  padding: 10px;
  text-decoration: none;
}
.huseyn__buttons a:hover {
  color: #018475;
  background-color: white;
  border: 2px solid #018475;
}
.huseyn hr {
  color: #018475;
}

.images__t {
  background: url(../img/psy.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 600px;
}

.added1,
.added2,
.added3,
.added4 {
  display: none;
}

.consulting__head h2 {
  color: #fff;
  text-align: center;
  padding-top: 26px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}
.consulting__head h2 span {
  color: #fff;
}
.consulting__carts {
  background: linear-gradient(to right, #018475 20%, #015a50 80%);
  padding: 10px;
  margin-top: 25px;
  color: #fff;
  text-align: center;
  border-radius: 10px;
  min-height: 400px;
  transition: 0.2s ease-in;
}
.consulting__carts span {
  font-size: 24px;
}
.consulting__carts:hover {
  box-shadow: 0px 2px 4px 3px #acacac;
}
.consulting__buttons button {
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 3px 6px;
  transition: 0.2s ease-in;
}
.consulting__buttons button:hover {
  background-color: #018475;
  color: #fff;
  border: 1px solid #fff;
}
.consulting__buttons .backs1,
.consulting__buttons .backs2,
.consulting__buttons .backs3,
.consulting__buttons .backs4 {
  display: none;
}

.clients {
  background: url(../img/fon.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 400px;
}
.clients__inner {
  padding: 15px;
  background-color: #fff;
  border-radius: 4px;
  opacity: 0.6;
  min-height: 250px;
  text-align: center;
  padding-top: 100px;
  color: #018475;
  transition: 0.2s ease-in;
}
.clients__inner:hover {
  opacity: 0.9;
  box-shadow: 0px 0px 2px 8px #fff;
}
.clients__number p {
  font-size: 46px;
}

.container-fluid {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.nihad__images img {
  width: 100%;
  box-shadow: 2px 3px 4px 3px grey;
}
.nihad__infos {
  text-align: center;
  align-self: center;
}
.nihad__infos h2 {
  font-size: 45px;
  color: #018475;
}
.nihad__infos p {
  font-size: 22px;
}
.nihad__blocks {
  flex-direction: column;
}
.nihad__blocks h4 {
  text-align: center;
  color: #018475;
}
.nihad__amulets:hover img {
  opacity: 1;
}
.nihad__imgss img {
  width: 100%;
  border-radius: 4px;
  opacity: 0.8;
  transition: 0.2s ease-in;
}
.nihad__prices {
  text-align: center;
  display: none;
  border-radius: 4px;
  padding: 5px;
  background-color: #018475;
}
.nihad__prices span {
  color: #fff;
}
.nihad__buttons {
  text-align: center;
}
.nihad__buttons button {
  background-color: #018475;
  border: none;
  border-radius: 4px;
  padding: 6px 16px;
  color: #fff;
}
.nihad__terapi-texts {
  border: 2px solid #018475;
  border-radius: 7px;
  padding: 6px;
}
.nihad__terapi-prs p {
  background-color: #018475;
  padding: 6px;
}